<template>
  <div>
    <main> <!-- Début du main -->
        <form class="admin">
          <h1>Ajouter une œuvre à {{getPerson.name}} </h1>
          <label>Nom:<input v-model="artwork.name" @input="propose(artwork.name)"></label><br>
          <label>Lien: <input v-model="artwork.slug" @input="propose(artwork.slug)"></label><br>
          <label>Date: <input v-model="artwork.date"></label><br>
          <!-- <label>Auteur: <input v-model="artwork.person"></label><br> -->
          <h3>Contenu</h3>
          <editor v-model="artwork.content"
          :options="defaultOptions"
          height="700px"
          previewStyle="vertical"
          mode="wysiwyg"
          ref="content"/>
          <br>
          <h3>Notes</h3>
          <editor v-model="artwork.notes"
          :options="defaultOptions"
          height="300px"
          previewStyle="vertical"
          mode="wysiwyg"
          ref="notes"/>
          <br>
          <h3>Texte</h3>
          <editor v-model="artwork.text"
          :options="defaultOptions"
          height="500px"
          previewStyle="vertical"
          mode="wysiwyg"
          ref="text"/>
          <p><br><b>La fiche aura par défaut le badge "tout juste créé"</b></p>
          <input type="button" value="Annuler" @click="$router.push('/'+getPerson.slug)">
          <input type="button" value="Créer" @click="save()">
          <BarLoader v-show="loading" style="margin: 0 auto; border-radius:5px, display: none"></BarLoader>
        </form>
    </main><!-- fin main -->
  </div>
</template>
<script>
// @ is an alias to /src
import { mapActions, mapGetters } from 'vuex'
import { Editor } from '@toast-ui/vue-editor'


export default {
  name: 'home',
  components: {
    Editor
  },
  data () {
    return {
        artwork: {date:""},
        loading: false,
        defaultOptions: {
            language: 'fr_FR',
            useCommandShortcut: true,
            useDefaultHTMLSanitizer: true,
            usageStatistics: true,
            hideModeSwitch: false,
            toolbarItems: [
                'heading',
                'bold',
                'italic',
                'strike',
                'divider',
                'hr',
                'quote',
                'divider',
                'ul',
                'ol',
                'indent',
                'outdent',
                'divider',
                'table',
                'image',
                'link',
                'divider',
                'code',
                'codeblock'
            ]
        }
    }
  },
  // Fetches posts when the component is created.
  created () {
    window.scrollTo(0, 0)
    this.artwork.person = this.getPerson.name
  },
  computed: {
        ...mapGetters('jwt',[
        'jwt',
        'jwtSubject',
        'jwtIssuer'
        ]),
        ...mapGetters('persons', [
          'getPerson'
        ])
  },
  methods: {
    ...mapActions('artworks',[
      'createArtwork',
      'setArtworks'
    ]),
    ...mapActions('alert',[
      'newAlert'
    ]),
    getHtml() {
        return this.$refs.Editor.invoke('getMarkdown')
        // return this.$refs.Editor.invoke('getHtml') -> check github @toast-ui.vue-editor issues, if getHtml method works
    },
    propose(input) {
        this.artwork.slug = (input.replace(/\W|\s+|-+/g, "-")).toLowerCase()
    },
    save() {
      this.loading = true;
        this.createArtwork({
            slug: this.artwork.slug,
            name: this.artwork.name, 
            state: "beginned", 
            date: this.artwork.date, 
            person: this.artwork.person, 
            content: this.$refs.content.invoke('getMarkdown'),
            text: this.$refs.text.invoke('getMarkdown'),
            notes: this.$refs.notes.invoke('getMarkdown')
            })
            .then(r=>{
                this.newAlert({type:'success', content:'Successfully edited'})
                this.setArtworks(r.data)
                this.$router.push('/artwork/'+this.artwork.slug)
            })
            .catch(e=>{
                this.loading = false;
                this.newAlert({type:'error', content:e.response.data})
            })
    }
  }
}
</script>
